.SelectComp {
  display: inline-block;
  width: 100%;
  position: relative;
  margin-bottom: 15px; }
  .SelectComp:hover label::before {
    border-bottom: 1px solid #000; }
  .SelectComp:focus label::before {
    border-bottom: 1px solid #000; }
  .SelectComp label::before {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0px;
    z-index: 2;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.29); }
    .SelectComp label::before:hover {
      border-bottom: 1px solid #000; }
  .SelectComp label:focus {
    outline: none !important;
    border-bottom: 1px solid #2196f3; }
  .SelectComp > select {
    width: 100%;
    height: 100%;
    border: 0;
    font-size: 14px;
    line-height: 30px;
    border-bottom: 1px solid #d2d2d2; }
    .SelectComp > select:focus {
      outline: none !important;
      border-bottom: 1px solid #2196f3; }
    .SelectComp > select:focus::before {
      border-bottom: 1px solid #2196f3; }



