.spinnerWrapper {
  position: fixed;
  width: 100%;
  text-align: center;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 9999; }
  .spinnerWrapper div.spinnerBg {
    background: #00000061;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0; }
  .spinnerWrapper > span {
    top: 200px; }



