.InputCompWrapper {
  display: inline-block;
  width: 100%;
  position: relative;
  margin-bottom: 15px; }
  .InputCompWrapper:hover label::before {
    border-bottom: 1px solid #000; }
  .InputCompWrapper:hover label:focus {
    outline: none !important;
    border-bottom: 1px solid #2196f3; }
  .InputCompWrapper label::before {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    bottom: 0px;
    z-index: 2;
    box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.16); }
    .InputCompWrapper label::before:hover {
      border-bottom: 1px solid #000; }
    .InputCompWrapper label::before:focus {
      outline: none !important;
      border-bottom: 1px solid #2196f3; }
  .InputCompWrapper .InputComp {
    width: 100%;
    border: 0;
    border-bottom: 1px solid #c5c5c5;
    padding: 8px 0;
    font-size: 14px; }
    .InputCompWrapper .InputComp:focus {
      outline: none !important;
      border-bottom: 1px solid #2196f3; }



